.page-header__logo{
  display: block ;
  box-sizing: border-box;
  min-height: 68px ;
  padding-top: 21px ;
  padding-left: 20px ;

  }
.page-header__logo-wrapper{
  position: relative;
  z-index: 1 ;

  display: flex;
  justify-content: space-between;
  margin: 0 ;
  box-sizing: border-box;

  background-color: @dark ;
  border-bottom: 1px solid @black ;
}

.page-header__logo-wrapper--closed{
  width: 320px ;

  background-color: rgba(0, 0, 0, 0.3) ;
  border: none ;
}
.page-header__logo-wrapper--nojs{
  position: static;

  background-color: @dark ;
  //border: 2px solid @very-dark ;
}
.page-header{
  width: 320px ;
  margin: auto;

  //background-color: rgba(232, 226, 67, 0.5) ;
}
.page-header__toggle{
  display: block;
  width: 90px ;
  height: 66px ;

  color: transparent ;

  border: none ;
  background: transparent ;

  @media (min-width: @desktop-width){
    display: none ;
  }
}

.page-header__toggle--closed{
 position: relative;
 background-image: url(../img/icon-menu-burger.svg) ;
 background-repeat: no-repeat;
 background-position: center;

  &:hover,
  &:active{
    background: none ;
  }

  &::before{
    content: ' ' ;

    position: absolute;
    top: 21px ;
    left: 21px ;

    width: 50px ;
    height: 4px ;
  }

  &:hover::before{
    background-color: @lipstick ;

    box-shadow: 0 10px 0 0 @lipstick, 0 20px 0 0 @lipstick ;
  }

  &:active::before{
    background-color: rgba(210, 40, 86, 0.3) ;

    box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3),
                0 20px 0 0 rgba(210, 40, 86, 0.3) ;
  }

}

.page-header__toggle--opened{
  position: relative;
  background-image: url(../img/icon-menu-cross.svg) ;
  background-repeat: no-repeat;
  background-position: 47px 20px ;

  &:hover,
  &:active{
    background: none ;
  }

  &::before,
  &::after{
    content: ' ' ;

    width: 30px ;
    height: 5px ;

    position: absolute;
    top: 29px ;
    left: 44px ;
  }

  &::before{
    transform: rotate(45deg);
  }

  &::after{
    transform: rotate(-45deg);
  }

  &:hover::before,
  &:hover::after{
    background-color: @lipstick ;
  }

  &:active::before,
  &:active::after{
    background-color: @lipstick ;
    opacity: 0.3 ;
  }
}

.page-header__toggle--nojs{
  display: none ;
}

@media (min-width: @tablet-width){
  .page-header{
    width: 660px ;
  }

  .page-header__logo{
    min-height: 110px ;
  }
}

@media (min-width: @desktop-width){
  .page-header{
    position: absolute;
    z-index: 10 ;

    width: 100% ;
    min-height: 110px ;
  }

  .page-header__wrapper{
    display: flex;
    justify-content: space-between;
    width: 1200px ;
    margin: auto;
  }

  .page-header__logo{
    min-height: 110px ;
    flex-basis: 150px ;
    margin-left: 130px ;
  }
}
