@font-face {
  font-family: OpenSans ;
  src: url(../fonts/opensans.woff), url(../fonts/opensans.woff2);
}
@font-face {
  font-family: OpenSans-Bold;
  src: url(../fonts/opensansbold.woff), url(../fonts/opensansbold.woff2);
}
@font-face {
  font-family: OpenSans-Light;
  src: url(../fonts/opensanslight.woff), url(../fonts/opensanslight.woff2);
}
.page-header__logo {
  display: block ;
  box-sizing: border-box;
  min-height: 68px ;
  padding-top: 21px ;
  padding-left: 20px ;
}
.page-header__logo-wrapper {
  position: relative;
  z-index: 1 ;
  display: flex;
  justify-content: space-between;
  margin: 0 ;
  box-sizing: border-box;
  background-color: #283645;
  border-bottom: 1px solid #000000;
}
.page-header__logo-wrapper--closed {
  width: 320px ;
  background-color: rgba(0, 0, 0, 0.3);
  border: none ;
}
.page-header__logo-wrapper--nojs {
  position: static;
  background-color: #283645;
}
.page-header {
  width: 320px ;
  margin: auto;
}
.page-header__toggle {
  display: block;
  width: 90px ;
  height: 66px ;
  color: transparent ;
  border: none ;
  background: transparent ;
}
@media (min-width: 1200px) {
  .page-header__toggle {
    display: none ;
  }
}
.page-header__toggle--closed {
  position: relative;
  background-image: url(../img/icon-menu-burger.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.page-header__toggle--closed:hover,
.page-header__toggle--closed:active {
  background: none ;
}
.page-header__toggle--closed::before {
  content: ' ';
  position: absolute;
  top: 21px ;
  left: 21px ;
  width: 50px ;
  height: 4px ;
}
.page-header__toggle--closed:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.page-header__toggle--closed:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.page-header__toggle--opened {
  position: relative;
  background-image: url(../img/icon-menu-cross.svg);
  background-repeat: no-repeat;
  background-position: 47px 20px ;
}
.page-header__toggle--opened:hover,
.page-header__toggle--opened:active {
  background: none ;
}
.page-header__toggle--opened::before,
.page-header__toggle--opened::after {
  content: ' ';
  width: 30px ;
  height: 5px ;
  position: absolute;
  top: 29px ;
  left: 44px ;
}
.page-header__toggle--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle--opened:hover::before,
.page-header__toggle--opened:hover::after {
  background-color: #d22856;
}
.page-header__toggle--opened:active::before,
.page-header__toggle--opened:active::after {
  background-color: #d22856;
  opacity: 0.3;
}
.page-header__toggle--nojs {
  display: none ;
}
@media (min-width: 660px) {
  .page-header {
    width: 660px ;
  }
  .page-header__logo {
    min-height: 110px ;
  }
}
@media (min-width: 1200px) {
  .page-header {
    position: absolute;
    z-index: 10 ;
    width: 100% ;
    min-height: 110px ;
  }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    width: 1200px ;
    margin: auto;
  }
  .page-header__logo {
    min-height: 110px ;
    flex-basis: 150px ;
    margin-left: 130px ;
  }
}
.visually-hidden {
